:root {
  --primary-font: "DM Sans", sans-serif;
  --main-text-color: #111;
  --accent-text: #4f2ee8;
  --additional-text: #b4afaf;
}

body {
  font-family: var(--primary-font);
}

ul {
  list-style: none;
}

img {
  display: block;
}

.home-wrapper {
  width: 1000px;
  margin: 40px 24px 40px 40px;
}

.category-container {
  margin-top: 40px;
}

.books-list {
  flex-direction: row;
  column-gap: 24px;
  margin: 0;
  padding-left: 0;
  display: flex;
}

.home-title {
  letter-spacing: -.04em;
  color: var(--main-text-color);
  margin-top: 40px;
  font-size: 48px;
  font-weight: 700;
  line-height: 1.08333;
}

.home-title-accent {
  color: var(--accent-text);
}

.home-book-category {
  letter-spacing: .03em;
  text-transform: uppercase;
  color: var(--additional-text);
  margin-top: 0;
  margin-bottom: 18px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71429;
}

.book-name {
  letter-spacing: -.04em;
  color: var(--main-text-color);
  margin-top: 14px;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.author-name {
  letter-spacing: -.04em;
  color: var(--additional-text);
  margin: 0;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.16667;
}

.btn-see-more {
  width: auto;
  height: 46px;
  border: 2px solid var(--accent-text);
  letter-spacing: -.01em;
  background-color: #fff;
  border-radius: 40px;
  flex-direction: row;
  align-items: flex-start;
  margin-top: 32px;
  margin-left: auto;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28571;
  transition: background-color 1s linear, transform .3s linear;
  display: flex;
}

.btn-see-more:hover {
  transform: scale(1.1);
  box-shadow: 0 .5em .5em -.4em;
}

